<template>
	<div class="myresponse-container">
		<div class="myresponse-header">
			<div class="myresponse-zhong">
				<div class="myresponse-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="myresponse-ti">我的问答</div>
				<div></div>
			</div>
		</div>
		<div class="myresponse-content">
			<div class="myresponse-vacancy" v-show="discussion_list.length==0">暂无数据</div>
			<div>
				<!--<div class="myresponse-shang">面对抖音</div>-->
				<div class="myresponse-bj" v-for="item in discussion_list">
					<div class="myresponse-wenz">{{item.comment_content}}</div>
					<!--<div class="myresponse-around">
						<div class="myresponse-tu">
							<img src="../assets/img_tl1.png">
						</div>
						<div class="myresponse-tu">
							<img src="../assets/img_tl2.png">
						</div>
						<div class="myresponse-tu">
							<img src="../assets/img_tl3.png">
						</div>
					</div>-->
					<div class="myresponse-between">
						<div class="myresponse-flex">
							<div class="myresponse-niming">匿名</div>
							<div class="myresponse-shijian">{{$root.formatDate(item.create_time)}}</div>
						</div>
						<div class="myresponse-flex">
							<div class="myresponse-xiaoxi">
								<img src="../assets/icon_pinglun.png">
							</div>
							<div class="myresponse-shuliang">0</div>
						</div>
					</div>
				</div>
			</div>
			<!--<div class="myresponse-bj">
				<div class="myresponse-wenz">抖音之所以能成为全球性的产品，是因为它真正融入了海外众多国家当地人的生活和社交关系链。</div>
				<div class="myresponse-around">
					<div class="myresponse-tu">
						<img src="../assets/img_tl1.png">
					</div>
					<div class="myresponse-tu">
						<img src="../assets/img_tl2.png">
					</div>
					<div class="myresponse-tu">
						<img src="../assets/img_tl3.png">
					</div>
				</div>
				<div class="myresponse-between">
					<div class="myresponse-flex">
						<div class="myresponse-niming">匿名</div>
						<div class="myresponse-shijian">2小时前</div>
					</div>
					<div class="myresponse-flex">
						<div class="myresponse-xiaoxi">
							<img src="../assets/icon_pinglun.png">
						</div>
						<div class="myresponse-shuliang">0</div>
					</div>
				</div>
			</div>-->
		</div>
		<div class="myresponse-footer">

		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				user_info: {},
				userid: '',
				discussion_list: [],
			}
		},
		methods: {
			back() {
				window.history.back();
			},
			discussion() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
					//					discussionid: that.discussionid
				};
				that.$http.post('/discussion/sel_usercomment', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.discussion_list = res.data;
						that.discussion_list = that.discussion_list.reverse();
					}
				})
			}
		},
		created() {
			// 页面刚加载的时候执行

			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}

			this.discussion();
		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.myresponse-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.myresponse-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.myresponse-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.myresponse-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.myresponse-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.myresponse-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	.myresponse-vacancy{
		font-size: 1.5rem;
		color: #999999;
		text-align: center;
		padding-top: 2rem;
	}
	.myresponse-shang {
		font-size: 1.7rem;
		color: #000000;
		background: #FFFFFF;
		padding: 1rem 0 0.5rem 1.5rem;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.myresponse-bj {
		background: #FFFFFF;
		margin-bottom: 1rem;
	}
	
	.myresponse-wenz {
		font-size: 1.4rem;
		color: #333333;
		padding: 0.3rem 2.6rem 1.2rem 1.5rem;
	}
	
	.myresponse-around {
		display: flex;
		justify-content: space-around;
	}
	
	.myresponse-tu img {
		width: auto;
		height: 9rem;
		margin-bottom: 1rem;
	}
	
	.myresponse-between {
		display: flex;
		justify-content: space-between;
	}
	
	.myresponse-flex {
		display: flex;
	}
	
	.myresponse-niming {
		font-size: 1.2rem;
		color: #8A8A8A;
		padding-left: 1.5rem;
		padding-right: 1rem;
	}
	
	.myresponse-shijian {
		font-size: 1.2rem;
		color: #8A8A8A;
	}
	
	.myresponse-xiaoxi img {
		width: auto;
		height: 1.2rem;
	}
	
	.myresponse-shuliang {
		font-size: 1.2rem;
		color: #8A8A8A;
		padding: 0 1rem 1.1rem 1rem;
	}
</style>


